  Josh's Custom CSS Reset
  https://www.joshwcomeau.com/css/custom-css-reset/
*/

*, *::before, *::after {
  box-sizing: border-box;
}

* {
  margin: 0;
}

body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}

img, picture, video, canvas, svg {
  display: block;
  max-width: 100%;
}

input, button, textarea, select {
  font: inherit;
}

p, h1, h2, h3, h4, h5, h6 {
  overflow-wrap: break-word;
}

p {
  text-wrap: pretty;
}
h1, h2, h3, h4, h5, h6 {
  text-wrap: balance;
}

ul, ol, li {
  list-style: none;
  padding: 0;
}

#root, #__next {
  isolation: isolate;
}

/* ^^ END OF RESET ^^ */

:root {
  --dark: #41576E;
  --mid:  #ACE;
  --light: #E0F0FF;
  --text: #1C3043;
}

body {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

button {
  border: none;
  border-radius: 3px;
  background-color: var(--light);
  padding: 0.2rem;
}

button:hover {
  cursor: pointer;
}

#wrapper {
  display: grid;
  grid-template-columns: 30ch 1fr;
  height: 100vh;
}

#sidebar {
  background-color: var(--dark);
  color: var(--light);
  padding: 1rem;
  /* border-right: 2px solid rgb(51, 78, 105); */
  box-shadow: 5px 2px 5px rgba(0,0,0,0.1);
}

#logo {
  text-align: center;
  padding-bottom: 2rem;
}

#wordmark {
  font-family: 'Shrikhand', serif;
  color: white;
  font-size: 3rem;
  text-decoration: underline;
}

#tagline {
  margin-top: -1rem;
}

.add-btn {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-repeat: no-repeat;
  padding-left: 1.5rem;
  width: 8em;
  text-align: center;
}

#project-bar {
  margin-top: 2rem;
}

#project-bar h2 {
  text-align: center;
}

#project-bar h2::after {
  display: block;
  content: "";
  border-top: .1rem solid var(--light);
  width: 100%;
  margin-bottom: 1rem;
}

#project-bar li {
  padding: 0.2rem;
  display: flex;
  justify-content: space-between;
}

#project-bar li:hover {
  background-color: rgba(0,0,0,0.1);
}

#project-bar .project button {
  border: none;
  background-color: transparent;
  color: var(--light);
}

#project-bar .delete-btn {
  background:url(${___CSS_LOADER_URL_REPLACEMENT_1___}) no-repeat;
  cursor:pointer;
  width: 2em;
  height: 2em;
  border: none;
}

.project button:hover {
  text-decoration: underline;
}

#main {
  background-color: var(--mid);
  padding: 1rem;
  color: var(--text);
}

.card-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  grid-auto-rows: 1fr;
  gap: 1rem;
  position: relative;
}

.task-card {
  background-color: var(--light);
  border-radius: 3px;
  padding: 1rem;
}

.task-card.hidden {
  display: none;
}

.task-card button {
  margin-right: 0.5rem;
}

.task-card .delete-btn {
  background:url(${___CSS_LOADER_URL_REPLACEMENT_2___}) no-repeat;
  cursor:pointer;
  width: 2em;
  height: 2em;
  border: none;
}

.task-card .edit-btn {
  background:url(${___CSS_LOADER_URL_REPLACEMENT_3___}) no-repeat;
  cursor:pointer;
  width: 2em;
  height: 2em;
  border: none;
}

.task-card button:hover {
  background-color: rgba(0,0,0,0.1);
}

.task-info-heading {
  font-weight: bold;
}

.task-card input[type="checkbox"] {
  appearance: none;
  position: relative;
  width: 1.5em;
  height: 1.5em;
  border: 2px solid var(--text);
  border-radius: 3px;
  color: var(--light);
}

.task-card input[type="checkbox"]:hover {
  cursor: pointer;
  box-shadow: inset 0 0 0 2px var(--text);
}

.task-card input[type="checkbox"]:checked {
  background-color: var(--dark);
}

.task-card input[type="checkbox"]::before {
  content: "";
  position: absolute;
  font-size: 1.5em;
  right: -1px;
  top: -0.3em;
  visibility: hidden;
}

.task-card input[type="checkbox"]:checked::before {
  visibility: visible;
}

dialog{
  width: 400px;
  margin: auto;
  border: none;
  color: var(--text);
  background-color: var(--light);
}

dialog::backdrop {
  background-color:rgba(0, 0, 0, 0.4);
}

dialog button {
  background-color: var(--dark);
  color: var(--light);
  padding: 5px 1rem;
}

dialog form {
  margin: auto;
  width: 95%;
}

dialog input, select, textarea {
  font-family: inherit;
  font-size: 100%;
  width: 100%;
  display: block;
  margin: auto;
  margin-bottom: 1rem;
  padding: 5px 0;
  resize: vertical;
}

dialog .buttons {
  display: flex;
  justify-content: center;
  gap: 1rem;
